<template>

    <div class="appBox" >
        <div v-html="adData.content"></div>
    </div>
</template>

<script>
    import { adDetail } from "@/api/index"
    import Toast from 'vant/lib/toast';
    import 'vant/lib/toast/style';

    export default {
        name: "AdDetail",
        data()
        {
            return {
                id:'',
                adData:[]
            }
        },
        mounted()
        {
            this.id = this.$route.query.id
            this.getAdDetail()
        },
        methods:{

            // 获取列表
            getAdDetail()
            {
                adDetail({id:this.id}).then(res => {

                    this.adData = res
                })
            }
        }
    }
</script>

<style scoped>
    .app p .wscnph {
        width: 100%;
    }
    img {
        width: 100%;
    }

</style>



